<template>
  <div>
    <el-menu
      class="headerNav"
      mode="horizontal"
      default-active="1"
      active-text-color="#FFA640"
    @select="handelSelect">
      <el-menu-item class="el-col-3 el-col-push-1" index="0" route="/">
        <div style="height: 100%;width: 100%;">
          <el-image src="../../../static/img/wehouseLogo.png" style="height: 125%;width: 100%;"/>
        </div>
      </el-menu-item>
      <el-menu-item class="el-col-2 el-col-push-4" index="1">我的空间</el-menu-item>
      <el-menu-item class="el-col-2 el-col-push-4" index="2">我的资料</el-menu-item>
      <el-menu-item class="el-col-1 el-col-push-14" index="3">返回首页</el-menu-item>
    </el-menu>
  </div>
</template>

<script>
export default {
  name: 'headerUserData',
  data: function () {
    return {
      indexPath: ''
    }
  },
  watch: {
    indexPath () {
      switch (this.indexPath) {
        case '0':
        case '3':
          this.$router.push('/')
          break
      }
    }
  },
  methods: {
    handelSelect (index, indexPath) {
      this.indexPath = index
      this.$emit('currentIndex', this.indexPath)
    }
  }
}
</script>

<style scoped>

</style>



