<!--
  个人资料页面
  功能需求 : 学生认证;展示房源收藏;展示已租信息;
-->
<template>
  <div>
    <header-user-data @currentIndex="changeIndex"/>
    <div v-show="currentIndex==='2'">
      头像URL: {{userData.userHead}}<br>
      用户名: {{userData.userName}}<br>
      用户手机号: {{userData.userPhone}}<br>
      是否学生认证: {{userData.studentAuthentication}}
    </div>

    <div v-show="currentIndex==='1'"></div>

    <Footer/>
  </div>
</template>

<script>
import Footer from '../components/Footer'
import HeaderUserData from '../components/headerUserData'

export default {
  name: 'userData',
  components: {HeaderUserData, Footer},
  data () {
    return {
      currentIndex: '',
      // 用户登录标识
      loginCookie: '',
      // 用户信息
      userData: {
        // 用户头像url
        userHead: '',
        // 昵称
        userName: '',
        // 手机号
        userPhone: '',
        // 是否学生认证
        studentAuthentication: false
      }
    }
  },
  methods: {
    // 根据用户的选择展示当前的选项卡
    changeIndex (e) {
      this.currentIndex = e
    }
  },
  created () {
    const that = this
    that.loginCookie = that.$cookie.get('token')

    // 请求获取个人信息
    that.$http.get('/i', {
      params: {
        Cookie: that.$cookie.get('token')
      }
    })
      .then(function (response) {
        console.log(response)
        if (response.status === 200) {
          console.log(response.data.errorCode + '\t' + response.data.errorMessage)
          if (response.data.errorCode !== 4103) {
            // 将请求到的数据写到用户信息中
            that.userData = response.data.data
          }
        } else {

        }
      })
      .catch(function (error) {
        console.log(error)
      })
      .then(function () {
        // always executed
      })
  }
}
</script>

<style scoped>

</style>



